import { Button } from "@/components/ui/button";

export function LoadingState({ label = "Carregando..." }: { label?: string }) {
  return (
    <section className="kv-surface rounded-3xl p-5" aria-live="polite">
      <p className="mb-3 text-sm font-semibold">{label}</p>
      <div className="space-y-3">
        <div className="h-4 w-40 animate-pulse rounded bg-muted" />
        <div className="h-4 w-full animate-pulse rounded bg-muted" />
        <div className="h-4 w-2/3 animate-pulse rounded bg-muted" />
        <div className="h-16 w-full animate-pulse rounded bg-muted" />
      </div>
    </section>
  );
}

export function EmptyState({
  title,
  description,
  ctaLabel,
  onCta,
}: {
  title: string;
  description: string;
  ctaLabel?: string;
  onCta?: () => void;
}) {
  return (
    <section className="kv-surface rounded-3xl p-5" aria-live="polite">
      <p className="text-sm font-semibold">{title}</p>
      <p className="mt-2 text-sm text-muted-foreground">{description}</p>
      {ctaLabel && onCta ? (
        <Button className="mt-4" variant="outline" onClick={onCta}>
          {ctaLabel}
        </Button>
      ) : null}
    </section>
  );
}

export function ErrorState({
  description,
  onRetry,
}: {
  description: string;
  onRetry?: () => void;
}) {
  return (
    <section className="kv-surface rounded-3xl border-destructive/35 p-5" aria-live="assertive">
      <p className="text-sm font-semibold text-destructive">Erro</p>
      <p className="mt-2 text-sm text-muted-foreground">{description}</p>
      {onRetry ? (
        <Button className="mt-4" onClick={onRetry}>
          Tentar novamente
        </Button>
      ) : null}
    </section>
  );
}

export function SuccessState({
  message = "Operação concluída com sucesso.",
}: {
  message?: string;
}) {
  return (
    <section className="kv-surface rounded-3xl p-5" aria-live="polite">
      <p className="rounded-md bg-primary/10 px-3 py-2 text-sm text-foreground">{message}</p>
    </section>
  );
}

export function PermissionState({
  title = "Sem permissão",
  description = "Você não tem acesso para visualizar esta tela.",
  ctaLabel,
  onCta,
}: {
  title?: string;
  description?: string;
  ctaLabel?: string;
  onCta?: () => void;
}) {
  return (
    <section className="kv-surface rounded-3xl p-5" aria-live="assertive">
      <p className="text-sm font-semibold">{title}</p>
      <p className="mt-2 text-sm text-muted-foreground">{description}</p>
      {ctaLabel && onCta ? (
        <Button className="mt-4" variant="outline" onClick={onCta}>
          {ctaLabel}
        </Button>
      ) : null}
    </section>
  );
}
